.wrp{
    width: 20px;
    height: 20px;
    position: relative;
    z-index: 999;
}
.wrp:hover{
    cursor: pointer;
}
.MenuIcon{
    background-color: black;
    width: 100%;
    height: 2px;
    position: absolute;
    top: calc(50% - 1px);
}
.MenuIcon::before , .MenuIcon::after{
    background-color: black;
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    left: 0;
}
.MenuIcon::before{
    top: 5px;
}
.MenuIcon::after{
    bottom: 5px;
}
.MenuIcona{
    background-color: transparent;
    width: 100%;
    height: 2px;
    position: absolute;
    top: calc(50% - 1px);
    opacity: 01;
}
.MenuIcona::before , .MenuIcona::after{
    background-color: black;
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    left: 0;
}
.MenuIcona::before{
    transform: rotate(45deg);
    top: 0;
}
.MenuIcona::after{
    top: 0;
    transform: rotate(-45deg);
}